iframe {
    pointer-events: none; /* Отключает взаимодействие с iframe */
}
.placeholder-container {
    position: relative;
}
.photo {
    width: 85%;
    margin: auto;
    height: 40vh;
    position: relative;
    cursor: pointer;
/*     background-color: black; */
    .photo-border {
        position: absolute;
        top: -5%;
        left: -5%;
        width: 105%; 
        height: 105%;
        pointer-events: none; 
        z-index: 10;

    }

}
.placeholder {
    position: absolute;
    width: 100%;
    height: 40vh;
    margin: auto;
    transition: opacity 1s ease-in;
}
.photo-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 30%;
    margin: auto;
    opacity: 1;    
    margin: 80px auto;
}
.photo-text {
    border: solid #442001 8px;
    padding: 0px 20px;
    margin-top: 5vh;
    font-size: 4vh;
    text-wrap: nowrap;
    line-height: 90px;
    text-align: center;
    font-weight: 700;
    font-style: normal;
    pointer-events: none;
    font-family: "Pacifico", serif;
    letter-spacing: 5px;
    color: #442001 ;
    background-color: #f4a460;
    border-radius: 30px;
}
.photo-border img {
    width: 105%; /* Граница адаптируется к размеру видео */
    height: 105%;
}
.react-player {
    background-color: black;
}
@media (max-width: 768px) {
    .photo-container {
        width: 80%;
        margin-bottom: 100px;
    }
    .photo-text__top {
        margin: 10px;

    }
    .photo {
        width: 100%;
    }
    .placeholder {
        width: 100%;
    }
}
@media (max-width: 480px) {
    .photo-container {
        width: 80%;
        margin-bottom: 30px;
    }
    .photo-text__top {
        margin: 10px;
        line-height: 10vh;
        font-size: 10vh;
    }
    .photo-text {
        line-height: 40px;
        font-size: 30px;
        text-wrap: wrap;
        padding: 5px 10px;
    }
    .photo {
        height: 200px;
        width: 100%;
    }
    .placeholder-container {
        height: 100%;
    }
    .placeholder {
        height: 100%;
    }
}
