.container {
    position: relative;   
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}
.background {
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
    position: fixed;
    height: 100vh;
}
.header-text {
    color: #442001;
    border-radius: 40px;
    border: solid 8px #442001 ;
    margin: 50px auto 0 auto;
    letter-spacing: 5px;
    padding: 10px 150px 20px;
    background-color: #ffb87a;
    font-family: "Pacifico", serif;
    font-size: 70px;
    font-weight: 700;
    font-style: normal;
    text-align: center;
}

@media (max-width: 480px) {
    .header-text {
        padding: 0 10px 15px;
        line-height: 45px;
        font-size: 40px;
        max-width: 80%;
    }
}