.backButton {
    font-weight: 700;
    font-style: normal;
    font-size: 25px;
    letter-spacing: 5px;
    position: absolute;
    z-index: 1000;
    padding: 0px 10px;
    top: 5%;
    left: 5%;
    color: #442001;
    background-color: #ffb575;
    font-family: "Pacifico", serif;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    border-radius: 20px;
    border: solid 4px #442001;
}

@media (max-width: 768px) {
    .backButton {
        top: 85%;
    }
}
@media (max-width: 480px) {
    .backButton {
        top: 85%;
        font-size: 15px;
        padding: 5px 5px;
    }
}