
.video-container {
    width: 100%;
    height: 100%;
}
.video {
    .video-border {
        position: absolute;
        top: -1%;
        left: -3%;
        width: 100%; 
        height: 100%;
        pointer-events: none; 
        z-index: 10;
    }
}
.video-container2 {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}
.video-text__top {
    font-family: "Pacifico", serif;
    letter-spacing: 5px;
    margin-bottom: 30px;
    font-size: 5vh;
    line-height: 5vh;
    text-align: center;
    font-weight: 700;
    font-style: normal;
    pointer-events: none;
    color: #442001;
    background-color: #ffb87a;
    padding: 15px 50px 30px;
    border-radius: 20px;
    border: solid 6px #442001;
}
video {
    width: 100%;
    height: 100%;
}
.video-text {
    margin-top: 1vh;
    font-size: 4vh;
    line-height: 4vh;
    text-align: center;
    font-weight: 700;
    font-style: normal;
    pointer-events: none;
    color: #cd1313;
}
@media (max-width: 768px) {
    .video-text__top {
        margin-top: 0;
        margin-bottom: 50px;
    }
    .video-container2 {
        height: 100vh;
        padding-top: 0;
    }
}
@media (max-width: 480px) {
    .video-text__top {
        max-width: 80%;
        font-size: 30px;
        line-height: 30px;
        font-weight: 700;
        padding: 10px;
        margin-top: 0;
        margin-bottom: 20px;
    }
    .video-container2 {
        padding-top: 0;

    }
    .video {
        max-width: 90%;
    }
}